import React, {Component} from "react"


class MemeGenerator extends Component{
    constructor(){
        super()
        this.state={
            topText:"",
            bottomText:"",
            randomImg:"http://i.imgflip.com/1bij.jpg",
            altText:"Soon...",
            allMemeImgs:[]
        }
        this.handleChange = this.handleChange.bind(this)
        this.handleClick = this.handleClick.bind(this)
    }
    componentDidMount(){
        fetch("https://api.imgflip.com/get_memes")
        .then(response => response.json())
        .then(response => {
            const {memes} = response.data
            this.setState({allMemeImgs:memes})

                // var canvas = this.state.randomImg
                // var context = canvas.getContext("2d");
                // var imageObj = new Image();
                // imageObj.onload = function () {
                //     context.drawImage(imageObj, 10, 10);
                //     context.font = "20px Calibri";
                //     context.fillText("My TEXT!", 50, 200);

                //     // open the image in a new browser tab
                //     // the user can right-click and save that image
                //     var win = window.open();
                //     win.document.write("<img src='" + canvas.toDataURL() + "'/>");

                // };
                // imageObj.src = "mail-image.jpg";
            ;})
    }
    handleChange(event){
        const {name, value} = event.target
        this.setState({[name]:value})
 
    }
    handleClick(e){
        e.preventDefault()
        const randNum = Math.floor(Math.random() * this.state.allMemeImgs.length)
        this.setState({randomImg:this.state.allMemeImgs[randNum].url})
        this.setState({ altText: this.state.allMemeImgs[randNum].name })
    }
    render(){
        return(
            <div>
                <form className="meme-form">
                    <input
                        name="topText"
                        type="text"
                        placeholder="Top Text"
                        value={this.state.topText}
                        onChange={this.handleChange}/>
                    <input
                        type="text"
                        name="bottomText"
                        placeholder="Bottom Text"
                        value={this.state.bottomText}
                        onChange={this.handleChange}/>
                <button onClick={this.handleClick}>Gen</button>
                </form>
                <div className="meme">
                    <img src={this.state.randomImg} alt={this.state.altText}/>
                    <h2 className="top">{this.state.topText}</h2>
                    <h2 className="bottom">{this.state.bottomText}</h2>
                </div>
            </div>
        )
    }
}

export default MemeGenerator